<template>
  <el-select
    v-model="value"
    multiple
    filterable
    remote
    reserve-keyword
    placeholder="Please enter a keyword"
    :remote-method="remoteMethod"
    @change="handleChange"
  >
    <el-option
      v-for="({ title, label }, key) in options"
      :key="key"
      :label="title"
      :value="title"
    />
  </el-select>
</template>

<script>
  export default {
    props: {
      options: {
        type: Array,
        default: () => []
      },
      multiple: {
        type: Boolean,
        default: false
      },
      rendered: {
        type: Array,
        default: () => []
      }
    },
    data: () => ({
      value: [],
      list: []
    }),
    watch: {
      rendered(value) {
        this.value = value
      }
    },
    methods: {
      remoteMethod(query) {
        this.$emit('onSearch', query)
      },
      handleChange(value) {
        this.$emit('onChange', value)
      }
    }
  }
</script>



